<template>
  <view>
    <view class="box">
      <view>
        <image class="box-icon" mode="widthFix" :src="$kt.file.byPath('/image/icon/star.svg')"></image>
      </view>
      <view style="font-weight: bold">
        {{ text }}
      </view>
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      text: "",
    };
  },
  mounted() {
    this.login();
  },
  methods: {
    async login() {
      this.text = "正在进行登录";
      var code = "";
      await new Promise((resolve, reject) => {
        uni.login({
          provider: "weixin",
          success: (loginRes) => {
            code = loginRes.code;
            resolve(loginRes);
          },
          fail: (err) => {
            reject(err);
          },
        });
      });

      this.$kt.request.request({
        api: this.$api.thirdParty.wechatUser.loginInMp,
        data: {
          code: code,
        },
        stateSuccess: (res) => {
          // 存储token
          this.$kt.request.setToken(res.data.token);
          this.$selfInfo.set(res.data.user);
          this.getBalanceSelfMap();

          // 判断用户信息的头像或昵称是否为空
          if (res.data.user.fileIdOfAvatar == null || res.data.user.nickname == null) {
            
          // uni.$emit 确认登录状态
          uni.$emit("loginStatus", true);

            uni.redirectTo({
              url: "/pages/init/selfInfoInit",
            });
            return false;
          }

          // 判断之前还有没有页面,uniapp
          if (getCurrentPages().length > 1) {
            uni.$emit("loginSuccess", true);
          // uni.$emit 确认登录状态
          uni.$emit("loginStatus", true);

            uni.navigateBack();
            return false;
          }

          uni.$emit("loginSuccess", true);
          // uni.$emit 确认登录状态
          uni.$emit("loginStatus", true);

          // 存储用户信息
          this.$selfInfo.set(res.data.user);

          uni.reLaunch({
            url: "/pages/body/static",
          });
        },
      });

      this.text = "登录成功";
    },
    getBalanceSelfMap() {
      this.$kt.request.request({
        api: this.$api.system.balance.getSelfMap,
        stateSuccess: (res) => {
          this.$system.setBalanceMap(res.data);
        },
      });
    },
  },
};
</script>

<style lang="scss">
.box {
  // 黑色滤镜
  position: fixed;
  filter: grayscale(100%);
  text-align: center;
  width: 100%;
  top: 50%;
  transform: translateY(-50%);
  font-size: 40rpx;
}

.box-icon {
  width: 200rpx;
  height: 200rpx;
  // 旋转动画
  animation: rotate 2s linear infinite;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}
</style>
